<template>
    <div class="box">
        <h2>排行榜</h2>
        <ul class="list">
            <li v-for="(item,i) in phb" :key="i" @click="rpclass(i);aa(item.api)"  >{{item.name}}</li>
        </ul>
        <ul class="dq">
            <router-link :to="{path:'/xq',query:{ids:val.tit,urls:val.src}}" tag="li" v-for="(val,i) in context"  :key="i">
                <img v-bind:src='val.src' v-bind:alt="val.tit">
                <p>{{val.tit}}</p>
            </router-link>
        </ul>
    </div>
</template>
<script>
export default {
    data(){
       return {
            phb:[{"name":"热血","api":"rexue"},{"name":"机战","api":"jizhan"},{"name":"运动","api":"yundong"},{"name":"推理","api":"tuili"},
                {"name":"冒险","api":"maoxian"},{"name":"搞笑","api":"gaoxiao"},{"name":"战争","api":"zhanzhen"},{"name":"玄幻","api":"xuanhuan"},
                {"name":"神魔","api":"shenmo"},{"name":"竞技","api":"jingji"},{"name":"都市","api":"dushi"},{"name":"霸总","api":"bazong"},
                {"name":"穿越","api":"chuanyue"},{"name":"社会","api":"shehui"},{"name":"恋爱","api":"lianai"},{"name":"悬疑","api":"xuanyi"},
                {"name":"宠物","api":"chongwu"},{"name":"吸血","api":"xixue"},{"name":"后宫","api":"hougong"},{"name":"真人","api":"zhenren"},
                {"name":"御姐","api":"yujie"},{"name":"古风","api":"gufeng"},{"name":"历史","api":"lishi"},{"name":"漫改","api":"mangai"},
                {"name":"恐怖","api":"kongbu"},{"name":"游戏","api":"youxi"},{"name":"科幻","api":"kehuan"},{"name":"武侠","api":"wuxia"},
                {"name":"修真","api":"xiuzhen"},{"name":"生活","api":"shenghuo"},{"name":"动作","api":"dongzuo"}
            ],
            context:[],
            urls:"",
       }
    },
    created(){
        var that= this;
        var data = that.$route.query.ln;
        if(data){
            that.urls="https://m.manhuatai.com/sort/"+data+".html"
        }else{
            that.urls="https://m.manhuatai.com/sort/xuanhuan.html"
        }
        that.getdata(that.urls)
       
    },
    mounted(){
        var that = this
        var data = that.$route.query.ln;   
        if(data == "都市"){
            that.rpclass(3)
        }else if(data == "悬疑"){
            that.rpclass(9)
        }else if(data == "热血"){
            that.rpclass(1)
        }else if(data == "恐怖"){
            that.rpclass(11)
        }else{
            that.rpclass(0)
        } 
    },
    methods:{
        aa(urls){
            this.urls="https://m.manhuatai.com/sort/"+urls+".html"
            this.getdata(this.urls)
        },
        getdata(url){
            var that = this
            that.context=[];
            that.axios.get(url).then(
                function(res){
                    var shuju = res.data
                    var hres=shuju.match(/class="thumbnail"((?!alt=).)*alt=/ig)
                    hres=hres.join()
                    hres=hres.match(/data-src((?!(jpg)|(png)|(JPG)).)+((jpg)|(png)|(JPG))/ig).join()
                    hres=hres.match(/\/\/((?!(jpg)|(png)|(JPG)).)+((jpg)|(png)|(JPG))/ig).join().replace(/\//ig,"").replace(/commh/ig,"com/mh/").split(",")
                    var tits=shuju.match(/class="title">([\u4E00-\u9FFF]|[a-zA-Z]|[0-9])+/ig).join();
                    tits=tits.match(/>([0-9]|[a-zA-Z]|[\u4E00-\u9FFF])+/ig).join().replace(/<|>|\//ig,"").split(",")
                    for(var i=0;i<hres.length;i++){
                        var obj = {};
                        obj.tit = tits[i];
                        obj.src ="https://"+hres[i]+"-480x640.webp";
                        that.context.push(obj)
                    }
                }
            )
        },
        rpclass(index){
            var lis = document.getElementsByClassName("list")[0].getElementsByTagName('li')
            for(var i=0;i<lis.length;i++){
                lis[i].className=""
            }
            lis[index].className='now'
        }
    }
}
</script>
<style scoped>
    .now{
        background-color:#ececec;
        border-radius: 10px;
    }
    .box{
         background-color: #cccccc;
    }
    .box h2{
        width: 100%;
        text-align: center;
    }
    .list{
        display: flex;
        justify-content:flex-start;
        border: 1px solid #ececec;
        flex-wrap: wrap;
        padding:5px;
    }
    .list li{
        font-size: 18px;
        width:44px;
        margin: 2px;
    }
    .dq{
        width: 100%;
        display: flex;
        justify-content:space-between;
        flex-wrap:wrap;
    }
    .dq li{
        width:33%;
        border: 1px solid #ececec;
    }
    .dq li img{
        width: 100%;
    }
</style>